// import axios from "../../config/axios-config/axios-instance";
import actions from './actions';
import { rtdb } from "../../library/firebase/firebase";

export const isConnected = () => {
    return dispatch => {
        let isOnline = rtdb.ref(".info/connected");
        dispatch(actions.connectionChecking());
        isOnline.on("value", function (snapshot) {
            if (snapshot.val() === true) {
                dispatch(actions.connectionActive());
            } else {
                dispatch({ type: '' })
                dispatch(actions.connectionClosed());
            }

        });
    }
}

export const clearMessage = () => {
    return dispatch => {
        dispatch(actions.clearMessage());
    }
}